.App {
	text-align: center;
}

.App-logo {
	-webkit-animation: App-logo-spin infinite 20s linear;
	        animation: App-logo-spin infinite 20s linear;
	height: 50px;
}

.App-header {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-line-pack: center;
	    align-content: center;
	background-color: #222;
	height: 150px;
	padding: 20px;
	margin-bottom: 50px;
	color: white;
}

.App-react-app {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-item-align: center;
	    align-self: center;
}

.App-title {
	font-size: 1.5em;
}

.App-intro {
	font-size: large;
}

@-webkit-keyframes App-logo-spin {
	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

#drum-machine {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-line-pack: center;
	    align-content: center;
	max-width: 600px;
	margin: 0 auto;
	background-color: #2f2f2f;
	border-radius: 10px;
	-webkit-box-shadow: black 3px 3px 5px;
	        box-shadow: black 3px 3px 5px;
	color: white;
}

#drum-machine > div {
	padding: 20px;
}

#drumPads {
	display: -ms-flexbox;
	display: flex;                       /* establish flex container */
	width: 63%;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;                     /* enable flex items to wrap */
	-ms-flex-pack: center;
	    justify-content: center;
}

#brand {
	-ms-flex: 100% 1;
	    flex: 100% 1;
	margin-left: 10px;
	margin-bottom: 10px;
	text-align: left;
	font-size: 1.2em;
	font-weight: 800;
	font-style: italic;
	letter-spacing: 0.1em;
}

.drum-pad {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex: 0 0 30%;
	    flex: 0 0 30%;                       /* don't grow, don't shrink, width */
	height: 100px;
	margin: 5px;
	border-radius: 10px;
	background-color: #616161;
	-webkit-box-shadow: #de0000 3px 3px 5px;
	        box-shadow: #de0000 3px 3px 5px;
	color: black;
	cursor: pointer;
}

.drum-pad span {
	-ms-flex-item-align: center;
	    align-self: center;
	font-weight: 800;
}

.drum-pad.hover {
	-webkit-box-shadow: none;
	        box-shadow: none;
	margin-bottom: 0px;
	margin-top: 10px;
	background-image: -webkit-radial-gradient(circle, white, #62ff00, #00ff14);
	background-image: -o-radial-gradient(circle, white, #62ff00, #00ff14);
	background-image: radial-gradient(circle, white, #62ff00, #00ff14);
}

#display,
#display > div {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: space-evenly;
	    justify-content: space-evenly;
}

#display .title {
	font-size: 1.1em;
	font-weight: 800;
	font-style: italic;
	letter-spacing: 0.1em;
	margin-bottom: 5px;
}

#display .info {
	background: #942727;
	border-radius: 5px;
	padding: 5px 0px;
}

#drum-volume {
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	cursor: pointer;
	outline: none;
}


